<template>
	<view class="container">
		<view>
			<view class="example-body">
				<uni-nav-bar :fixed="true" color="#333333" background-color="#FFFFFF" right-text="搜索" @clickLeft="handle_back" @clickRight="handleSearchOpen">
					<block slot="left"><text class="iconfont iconfanhui f18"></text></block>
					<view class="input-view search_input">
						<text class="iconfont iconsousuo" style="display: inline-block;"></text>
						<input
							confirm-type="search"
							v-model="params.networkTitleContent"
							class="nav-bar-input input_content"
							style="display: inline-block;"
							type="text"
							placeholder="输入搜索关键词"
							@input="input_networkTitle"
							@blur="input_networkTitle"
							@confirm="handler_keyup"
						/>
					</view>
					<block slot="right"><text></text></block>
				</uni-nav-bar>
			</view>
		</view>
		<view v-if="isHistory">
			<view class="index_list">
				<!--S 诉求列表 -->
				<view v-for="item in list" class="uni-divider-line mb15">
					<view @click="handleDetail(item)">
						<view class="uni-flex-list mb15">
							<text class="list_name ">{{ handler_hide(item.submitterName) }}</text>
							<view class="list_num">
								<text>诉求编号：</text>
								<text>{{ item.networkId }}</text>
							</view>
						</view>
						<view class="list_name font-weigth-none mb15" v-html="ruleTitle(item.networkContent)"></view>
						<view class="uni-flex-list_img">
							<view class="img" v-for="(itemson, index) in item.networkFile">
								<image :src="imgPath + itemson" alt="" class="uni_width uni_height" @error="handle_image_error" mode="aspectFill"></image>
							</view>
						</view>
					</view>
					<view class="uni-flex-list mb15">
						<text class="list_time">{{ item.submitTime }}</text>
						<view @click="handleFollow(item)">
							<image v-if="item.isFollow == '1'" src="../../static/images/heart_default.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
							<image v-if="item.isFollow == '2'" src="../../static/images/heart.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
							<text class="pl8">{{ item.isFollowDesc }}</text>
						</view>
					</view>
					<!--E  诉求列表 -->
				</view>
			</view>
			<view class="uni-loadmore" v-if="showLoadMore" style="background-color: #FFFFFF;">{{ loadMoreText }}</view>
		</view>
		<view v-else class="no-data">没有搜索到相关内容</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			title: '平城热线',
			imgPath: this.$config.imgPath,
			params: {
				page: 1,
				limit: 10,
				userId: '',
				networkTitle: '',
				networkTitleContent: ''
			},
			list: [],
			imageError: false,
			isHistory: true,
			loadMoreText: '加载中...',
			showLoadMore: false,
			loadingType: 0,
			keyword_show: false,
			keyword: ''
		};
	},

	onLoad() {
		// #ifdef H5
		// this.headerTop = document.getElementsByTagName('uni-page-head')[0].offsetHeight+'px';
		// #endif
		this.params.userId = uni.getStorageInfo('submitterId');
		// this.loadData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.params.page = 1;
		this.list = [];
		this.loadData();
	},
	//加载更多
	onReachBottom() {
		if (!this.showLoadMore) {
			this.showLoadMore = true;
			this.params.page += 1;
			this.loadData();
		}
	},
	/*E 导航 搜索按钮*/
	methods: {
		/*S 文字高亮*/
		ruleTitle(items) {
			if(items){
				this.keyword = uni.getStorageSync('keyword');
				let title = items;
				let rep = new RegExp(this.keyword, 'g');
				let resDtring = `<span style='color:#4A93FE;'>${this.keyword}</span>`;
				return title.replace(rep, resDtring);
			}
		},
		/*E 文字高亮*/
		/*S 返回*/
		handle_back() {
			uni.removeStorageSync('keyword')
			uni.navigateBack();
		},
		/*E 返回*/
		/*S 初始数据*/
		loadData() {
			let _this = this;
			this.loadingType = 0;
			this.list = [];
			this.$minApi.networkWorkOrderPage({ ..._this.params }).then(res => {
				var keyword = uni.getStorageSync('keyword');
				if (res.data) {
					if (res.data.result.length < this.params.limit) {
						this.showLoadMore = true;
						this.loadMoreText = '没有更多数据了';
					} else {
						this.showLoadMore = false;
					}
					if (res.data.result.length > 0) {
						res.data.result.map(i => {
							if (i.networkFile) {
								i.networkFile = JSON.parse(i.networkFile);
							}
							if (i.networkTitle == keyword || i.networkContent == keyword) {
								i.keyword_show = true;
							}
						});
						this.list = this.list.concat(res.data.result);
						this.isHistory = true;
					} else {
						this.isHistory = false;
					}
				}
			});
		},
		/*E 初始数据*/
		/*S 导航 搜索按钮 */
		handleSearchOpen() {
			this.params.page = 1;
			this.list = [];
			this.loadData();
		},
		input_networkTitle(e) {
			this.params.networkTitleContent = e.detail.value;
			uni.setStorageSync('keyword', this.params.networkTitleContent);
		},
		handler_keyup(e) {
			this.params.networkTitleContent = e.detail.value;
			uni.setStorageSync('keyword', this.params.networkTitleContent);
			this.handleSearchOpen();
		},
		/*E导航 搜索按钮 */
		/*S 详情 */
		handleDetail(item) {
			let id = item.networkId;
			let submitterId = item.submitterId;
			uni.navigateTo({
				url: `/pages/detail?id=${id}&submitterId=${submitterId}`
			});
		},
		/*E 详情 */
		/*S 图片加载失败 */
		handle_image_error: function(index) {
			this.imageError = true;
		},
		/*E 图片加载失败 */
		/*S 取消关注 */
		handleFollow(item) {
			var followlist = {
				networkId: item.networkId,
				userId: uni.getStorageInfo('submitterId')
			};
			if (item.isFollow == '2') {
				// 关注
				this.$minApi.networkWorkOrderCancelFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						item.isFollow = '1';
						item.isFollowDesc = '未关注';
						uni.showToast({
							title: '取消关注成功'
						});
						
					} else {
						
					}
				});
			} else {
				// 未关注
				this.$minApi.networkWorkOrderFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						item.isFollow = '2';
						item.isFollowDesc = '已关注';
						uni.showToast({
							title: '关注成功'
						});
					} else {
					}
				});
			}
		},
		/*E 取消关注*/
		/*S 隐藏字符*/
		handler_hide(string) {
			let arrBox = [...string];
			var str = '';
			arrBox.map((res, index) => {
				if (index > 0) {
					str += '*';
				} else {
					str += res;
				}
			});
			return str;
		}
		/*E 隐藏字符*/
	}
};
</script>

