		font-size: 32upx;
		color: #333333;
		font-weight: 500;
	}
	.list_num {
		font-size: 26upx;
		color: #646464;
	}
	.list_time {
		color: #959595;
		font-size: 24upx;
	}
	.list_follow {
		width: 28upx;
